/* eslint-disable indent */
/* eslint-disable no-promise-executor-return */
import Vue from 'vue';
// eslint-disable-next-line import/no-extraneous-dependencies
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
// eslint-disable-next-line no-unused-vars

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    cartCount: 0,

    userAccessKey: null,
    cartLoadingggg: false,
    loadCartLoading: false,
    cartProductData: [],

    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductData = [];
    },

    updateCartProductAmount(state, { productId, amount }) {
      // eslint-disable-next-line no-shadow
      const item = state.cartProducts.find((item) => item.productId === productId);

      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
      // eslint-disable-next-line no-plusplus
      state.cartCount--;
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartLoadingggg = true;
      state.cartProductsData = items;
      state.cartLoadingggg = false;
    },
    syncCartProducts(state) {
      // eslint-disable-next-line arrow-body-style
      state.cartProducts = state.cartProductsData.map((item) => {
        return {
          productId: item.product.id,
          amount: item.quantity,
        };
      });
    },
  },
  getters: {
    cartDetailProducts(state) {
      state.cartLoadingggg = true;
      // eslint-disable-next-line arrow-body-style, no-promise-executor-return
      // eslint-disable-next-line no-undef

      setTimeout(() => {
        state.cartLoadingggg = false;
      }, 2000);

      // return (new Promise((resolve) => setTimeout(resolve, 2000)))
      return state.cartProducts.map((item) => {
        // eslint-disable-next-line prefer-destructuring
        const product = state.cartProductsData
          .find((p) => p.product.id === item.productId).product;
        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
        // eslint-disable-next-line arrow-body-style
      });
    },
    cartTotalPrice(state, getters) {
      // eslint-disable-next-line max-len, no-undef, arrow-body-style, no-trailing-spaces
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios
      // eslint-disable-next-line prefer-template
        .get(API_BASE_URL + '/orders/' + orderId, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          console.log('load order info start');
          context.commit('updateOrderInfo', response.data);
          console.log('load order info end');
        });
    },
    loadCart(context) {
      return axios
        // eslint-disable-next-line prefer-template
        .get(API_BASE_URL + '/baskets', {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
          });
    },
    addProductToCart(context, { productId, amount }) {
      this.state.loadCartLoading = true;
      // eslint-disable-next-line no-shadow, no-promise-executor-return
      return (new Promise((resolve) => setTimeout(resolve, 2000)))
      // eslint-disable-next-line arrow-body-style
        .then(() => {
          // eslint-disable-next-line prefer-template
          return axios.post(API_BASE_URL + '/baskets/products', {
          // eslint-disable-next-line object-shorthand
            productId: productId,
            quantity: amount,

          }, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          })
            .then((response) => {
              context.commit('updateCartProductsData', response.data.items);
              context.commit('syncCartProducts');
              // eslint-disable-next-line no-plusplus
              this.state.cartCount++;
              this.state.loadCartLoading = false;
            });
        });
    },
    deleteProductCart(context, productId) {
      context.commit('deleteCartProduct', { productId });
      // eslint-disable-next-line prefer-template
      return axios.delete(API_BASE_URL + '/baskets/products', {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
        data: {
          productId,
        },
      }).then((response) => {
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      });
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount });
      if (amount < 1) {
        return;
      }
      // eslint-disable-next-line prefer-template, consistent-return
      return axios.put(API_BASE_URL + '/baskets/products', {
        // eslint-disable-next-line object-shorthand
        productId: productId,
        quantity: amount,

      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },

  },
});
